import { SET_PRIME_1, SET_PRIME_2, SET_TIME_TO_CALCULATE, SET_RSA_PHI, SET_RSA_D, SET_RSA_N, SET_RSA_E } from '../actions/constants'

const initialState = {
    prime1: '',
    prime2: '',
    timeToCalculate: '0s',
    d: 0,
    phi: 0,
    n: 0,
    e: 17
}

const rsa = (state = initialState, action) => {
    switch(action.type) {
        case SET_RSA_E:
            if (!isNaN(action.e) && action.e !== null) {
                return {
                    ...state,
                    e: action.e
                }    
            }
            break
        case SET_RSA_PHI:
            return {
                ...state,
                phi: action.phi
            }
        case SET_RSA_D:
            return {
                ...state,
                d: action.d
            }
        case SET_RSA_N:
            return {
                ...state,
                n: action.n
            }
        case SET_PRIME_1:
            return {
                ...state,
                prime1: action.prime1
            }
        case SET_PRIME_2:
            return {
                ...state,
                prime2: action.prime2
            }
        case SET_TIME_TO_CALCULATE:
            return {
                ...state,
                timeToCalculate: action.timeToCalculate
            }
        default:
            return state
    }
}

export default rsa